<template>
  <div>
    <div
      class="p-5 text-center bg-image"
      style="
        background-image: url('https://i.imgur.com/eKMsNKn.png');
        height: 100%;
      "
    >
      <div
        style="width: 25%; margin: auto; padding-bottom: 15%; padding-top: 5%"
      >
        <div class="card text-center">
          <div class="card-header" style="font-weight: bold">
            Login To Baja's Lab
          </div>
          <div class="card-body">
            <form @submit.prevent="login" action="#">
              <!-- Email input -->
              <div class="form-outline mb-4">
                <input
                  type="email"
                  v-model="email"
                  id="email"
                  class="form-control"
                />
                <label class="form-label" for="email">Email address</label>
              </div>

              <!-- Password input -->
              <div class="form-outline mb-4">
                <input
                  type="password"
                  v-model="password"
                  id="password"
                  class="form-control"
                />
                <label class="form-label" for="password">Password</label>
              </div>

              <!-- Forgot Password -->
              <div class="row mb-4">
                <div class="col d-flex justify-content-center">
                  <div class="col">
                    <!-- Simple link -->
                    <a href="" @click.prevent="sendPasswordReset"
                      >Forgot password?</a
                    >
                  </div>
                </div>
              </div>

              <!-- Submit button -->
              <button
                :class="{ 'is-loading': loading }"
                type="submit"
                class="btn btn-primary btn-block"
              >
                Login
              </button>
              <br />
              <p>Or Login With</p>
              <button
                @click.prevent="loginWithGoogle"
                type="button"
                class="btn btn-primary btn-floating mx-1"
              >
                <i class="fab fa-google"></i>
              </button>
            </form>
          </div>
          <router-link class="card-footer text-muted" to="/signup"
            >Don't have an account? Sign Up</router-link
          >
          <p class="card-footer text-danger" v-if="errorMessage">
            {{ errorMessage }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { auth } from "../firebase";

import { loginWithGoogle } from "../loginUtils";

export default {
  data() {
    return {
      auth,
      email: "",
      password: "",
      errorMessage: "",
      loading: false,
    };
  },
  methods: {
    async loginWithGoogle() {
      loginWithGoogle();
      this.$router.push("/dash");
    },
    async sendPasswordReset() {
      const { value: email } = await this.$swal.fire({
        title: "Enter your Email",
        input: "email",
        inputPlaceholder: "Email",
        showCancelButton: true,
        inputValidator: (value) => {
          if (!value) {
            return "You need to write something!";
          }
        },
      });
      if (email) {
        await auth.sendPasswordResetEmail(email);
        this.$swal.fire({
          icon: "success",
          title: "Email Sent",
          text: "A password reset request has been made. Check your email!",
        });
      }
    },
    async login() {
      this.loading = true;
      this.errorMessage = "";
      try {
        if (this.email.length > 0 && this.password.length > 0) {
          await auth.signInWithEmailAndPassword(this.email, this.password);
          this.$router.push("/dash");
        } else {
          this.errorMessage = "Please fill all fields!";
        }
      } catch (error) {
        this.errorMessage = error.message;
      }
      this.loading = false;
    },
  },
};
</script>