import Vue from 'vue'
import App from './App.vue'
import { firestorePlugin } from 'vuefire'
import VueRouter from 'vue-router'
import VuewSweetAlert2 from 'vue-sweetalert2'

import 'sweetalert2/dist/sweetalert2.min.css';


import VueCompositionApi from '@vue/composition-api'

const options = {
  confirmButtonColor: '#a02b1c',
  cancelButtonColor: '#232323',
};



Vue.use(VuewSweetAlert2,options);
Vue.use(VueCompositionApi)

Vue.use(VueRouter)
Vue.use(firestorePlugin)

Vue.config.productionTip = false

import Home from './components/Home'
import Login from './components/Login'
import SignUp from './components/SignUp'
import Dashboard from './components/Dashboard'

const routes = [
  { path: '/', component: Home, meta: { title: "Home - Baja's Lab"} },
  { path: '/home', component: Home, meta: { title: "Home - Baja's Lab"} },
  { path: '/login', component: Login, meta: { title: "Login - Baja's Lab"} },
  { path: '/signup', component: SignUp, meta: { title: "Sign Up - Baja's Lab"} },
  { path: '/dash', component: Dashboard, meta: { title: "Dashboard - Baja's Lab"} },
  
]

const router = new VueRouter({
  mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
  // This goes through the matched routes from last to first, finding the closest route with a title.
  // e.g., if we have `/some/deep/nested/route` and `/some`, `/deep`, and `/nested` have titles,
  // `/nested`'s will be chosen.
  const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);

  // Find the nearest route element with meta tags.
  const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

  const previousNearestWithMeta = from.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

  // If a route with a title was found, set the document (page) title to that value.
  if(nearestWithTitle) {
    document.title = nearestWithTitle.meta.title;
  } else if(previousNearestWithMeta) {
    document.title = previousNearestWithMeta.meta.title;
  }

  // Remove any stale meta tags from the document using the key attribute we set below.
  Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode.removeChild(el));

  // Skip rendering meta tags if there are none.
  if(!nearestWithMeta) return next();

  // Turn the meta tag definitions into actual elements in the head.
  nearestWithMeta.meta.metaTags.map(tagDef => {
    const tag = document.createElement('meta');

    Object.keys(tagDef).forEach(key => {
      tag.setAttribute(key, tagDef[key]);
    });

    // We use this to track which meta tags we create so we don't interfere with other ones.
    tag.setAttribute('data-vue-router-controlled', '');

    return tag;
  })
  // Add the meta tags to the document head.
  .forEach(tag => document.head.appendChild(tag));

  next();
});

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')

