<template>
  <div>
    <div class="card border border-primary shadow-0">
      <div class="card-header">Account Details</div>
      <div class="card-body">
        <h5 class="card-title">Display Name</h5>
        <p class="card-text">
          If you would like to change your display name, use the button below.
          <br /><br />
          <button class="btn btn-default" @click="changeDisplayName">
            Change Display Name
          </button>
        </p>

        <h5 class="card-title">Email</h5>
        <p class="card-text">
          If you would like to change your email, use the button below.
          <br /><br />
          <button class="btn btn-default" @click="changeEmail">
            Change Email
          </button>
        </p>
      </div>
    </div>
    <br />
    <div class="card border border-danger shadow-0">
      <div class="card-header">Danger Zone</div>
      <div class="card-body">
        <h5 class="card-title">Delete Account</h5>
        <p class="card-text">
          If you would like to delete your account, use the button below. <br />
          This action is in accordance with GDPR.
          <br /><br />
          <button class="btn btn-danger" @click="deleteAccount">
            Delete Account
          </button>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { auth } from "../../firebase";

import { loginWithGoogle } from "../../loginUtils";

export default {
  name: "AccountSecurity",
  components: {},
  methods: {
    async deleteAccount() {
        async function confirmDeletion(swal,user) {
            swal
                  .fire({
                    title: "Are you sure you want to delete your account?",
                    text: "You won't be able to revert this!",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    confirmButtonText: "Yes, delete it!",
                  })
                  .then((result) => {
                    if (result.isConfirmed) {
                        user.delete()
                        .then(() => {
                        swal.fire({
                            icon: "success",
                            title: "Account Terminated",
                            text: "Have a nice day. =)",
                          });
                        })
                        .catch((error) => {
                          swal.fire({
                            icon: "error",
                            title: "Oops...",
                            html: error.message,
                          });
                        });
                    }
                  });
        }
      if (auth.currentUser.providerData[0].providerId == "google.com") {
        var user = await loginWithGoogle();
        await confirmDeletion(this.$swal,user);
      } else {
        const { value: currentEmail } = await this.$swal.fire({
          title: "Enter your current Email",
          input: "email",
          inputPlaceholder: "Your email address",
        });
        if (currentEmail) {
          const { value: currentPassword } = await this.$swal.fire({
            title: "Enter your current Password",
            inputPlaceholder: "Your current password",
            input: "password",
            inputValidator: (value) => {
              if (!value) {
                return "You need to write something!";
              }
            },
          });
          if (currentPassword) {
            auth
              .signInWithEmailAndPassword(currentEmail, currentPassword)
              .then(async (userCredential) => {
                await confirmDeletion(this.$swal,userCredential.user);
              })
              .catch((error) => {
                this.$swal.fire({
                  icon: "error",
                  title: "Oops...",
                  html: error.message,
                });
              });
          }
        }
      }
    },
    async changeEmail() {
      const { value: email } = await this.$swal.fire({
        title: "Enter your desired Email",
        input: "email",
        inputPlaceholder: "Enter your desired email address",
        showCancelButton: true,
      });
      if (email) {
        try {
          const { value: currentEmail } = await this.$swal.fire({
            title: "Enter your current Email",
            input: "email",
            inputPlaceholder: "Your email address",
          });
          if (currentEmail) {
            const { value: currentPassword } = await this.$swal.fire({
              title: "Enter your current Password",
              inputPlaceholder: "Your current password",
              input: "password",
              inputValidator: (value) => {
                if (!value) {
                  return "You need to write something!";
                }
              },
            });
            if (currentPassword) {
              auth
                .signInWithEmailAndPassword(currentEmail, currentPassword)
                .then(async (userCredential) => {
                  userCredential.user
                    .updateEmail(email)
                    .then(() => {
                      this.$swal.fire({
                        icon: "success",
                        title: "Email Changed",
                        text: "Your Email has been changed to " + email + "!",
                      });
                    })
                    .catch((error) => {
                      this.$swal.fire({
                        icon: "error",
                        title: "Oops...",
                        html: error.message,
                      });
                    });
                })
                .catch((error) => {
                  this.$swal.fire({
                    icon: "error",
                    title: "Oops...",
                    html: error.message,
                  });
                });
            }
          }
        } catch (error) {
          this.$swal.fire({
            icon: "error",
            title: "Oops...",
            html: error.message,
          });
        }
      }
    },
    async changeDisplayName() {
      const { value: displayName } = await this.$swal.fire({
        title: "Enter your desired Display Name",
        input: "text",
        showCancelButton: true,
        inputValidator: (value) => {
          if (!value) {
            return "You need to write something!";
          }
        },
      });
      if (displayName) {
        try {
          await auth.currentUser.updateProfile({
            displayName: displayName,
          });
          this.$swal.fire({
            icon: "success",
            title: "Display Name Changed",
            text: "Your display name has been changed to " + displayName + "!",
          });
        } catch (error) {
          this.$swal.fire({
            icon: "error",
            title: "Oops...",
            html: error.message,
          });
        }
      }
    },
  },
};
</script>
