<template>
  <div>
    <div
      class="p-5 text-center bg-image"
      style="
        background-image: url('https://i.imgur.com/eKMsNKn.png');
        height: 100%;
      "
    >
      <div
        style="width: 25%; margin: auto; padding-bottom: 15%; padding-top: 5%"
      >
        <div class="card text-center">
          <div class="card-header" style="font-weight: bold">
            Sign Up for Baja's Lab
          </div>
          <div class="card-body">
            <form @submit.prevent="signUp" action="#">
              <!-- Name input -->
              <div class="form-outline mb-4">
                <input
                  type="string"
                  v-model="displayName"
                  id="displayName"
                  class="form-control"
                />
                <label class="form-label" for="displayName">Display Name</label>
              </div>

              <!-- Email input -->
              <div class="form-outline mb-4">
                <input
                  type="email"
                  v-model="email"
                  id="email"
                  class="form-control"
                />
                <label class="form-label" for="email">Email address</label>
              </div>

              <!-- Password input -->
              <div class="form-outline mb-4">
                <input
                  type="password"
                  v-model="password"
                  id="password"
                  class="form-control"
                />
                <label class="form-label" for="password">Password</label>
              </div>
              <!-- Confirm Password input -->
              <div class="form-outline mb-4">
                <input
                  type="password"
                  v-model="confirmpassword"
                  id="confirmpassword"
                  class="form-control"
                />
                <label class="form-label" for="confirmpassword"
                  >Confirm Password</label
                >
              </div>

              <!-- Submit button -->
              <button
                :class="{ 'is-loading': loading }"
                class="btn btn-primary btn-block"
              >
                Sign Up
              </button>
            </form>
          </div>
          <router-link class="card-footer text-muted" to="/login"
            >Have an account? Login</router-link
          >
          <p class="card-footer text-danger" v-if="errorMessage">
            {{ errorMessage }}
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { auth } from "../firebase";

export default {
  data() {
    return {
      auth,
      displayName: "",
      email: "",
      password: "",
      confirmpassword: "",
      errorMessage: "",
      loading: false,
    };
  },
  methods: {
    async signUp() {
      this.loading = true;
      this.errorMessage = "";
      console.log("test");
      try {
        if (this.email.length > 0) {
          if (this.password == this.confirmpassword) {
            await auth
              .createUserWithEmailAndPassword(this.email, this.password)
              .then((userCredential) => {
                this.errorMessage = "Account Created";
                userCredential.user.updateProfile({
                  displayName: this.displayName,
                }).then(() => {
                  this.$router.push('/dash');
                });
                
              });
          } else {
            this.errorMessage = "Passwords do not match!";
          }
        } else {
          this.errorMessage = "Please fill all fields!";
        }
      } catch (error) {
        this.errorMessage = error.message;
      }
      this.loading = false;
    },
  },
};
</script>