<template>
  <div class="card border border-primary shadow-0">
    <div class="card-header">Account Security</div>
    <div class="card-body">
      <h5 class="card-title">Password</h5>
      <p class="card-text">
        If you would like to change your password, use the button below.
        <br /><br />
        <button class="btn btn-default" @click="changePassword">
          Change Password
        </button>
      </p>
    </div>
  </div>
</template>

<script>
import { auth } from "../../firebase";
import { loginWithGoogle } from "../../loginUtils";

export default {
  name: "AccountSecurity",
  components: {},
  methods: {
    async changePassword() {
      async function confirmPasswordChange(swal, user, newVerifyPassword) {
        await user
          .updatePassword(newVerifyPassword)
          .then(() => {
            swal.fire({
              icon: "success",
              title: "Password Changed",
              text: "Your password has been updated.",
            });
          })
          .catch((error) => {
            this.$swal.fire({
              icon: "error",
              title: "Oops...",
              html: error.message,
            });
          });
      }
      const { value: newPassword } = await this.$swal.fire({
        title: "Enter your new password",
        input: "password",
        inputPlaceholder: "New Password",
        showCancelButton: true,
        inputValidator: (value) => {
          if (!value) {
            return "You need to write something!";
          }
        },
      });
      if (newPassword) {
        const { value: newVerifyPassword } = await this.$swal.fire({
          title: "Verify your new password",
          input: "password",
          inputPlaceholder: "Verify new Password",
          showCancelButton: true,
          inputValidator: (value) => {
            if (!value) {
              return "You need to write something!";
            }
          },
        });
        if (newPassword == newVerifyPassword) {
          try {
            if (auth.currentUser.providerData[0].providerId == "google.com") {
              var user = await loginWithGoogle();
              await confirmPasswordChange(this.$swal, user, newVerifyPassword);
            } else {
              const { value: currentEmail } = await this.$swal.fire({
                title: "Enter your current Email",
                input: "email",
                inputPlaceholder: "Your email address",
              });

              if (currentEmail) {
                const { value: currentPassword } = await this.$swal.fire({
                  title: "Enter your current Password",
                  inputPlaceholder: "Your current password",
                  input: "password",
                  inputValidator: (value) => {
                    if (!value) {
                      return "You need to write something!";
                    }
                  },
                });
                if (currentPassword) {
                  auth
                    .signInWithEmailAndPassword(currentEmail, currentPassword)
                    .then(async (userCredential) => {
                      await confirmPasswordChange(
                        this.$swal,
                        userCredential.user,
                        newVerifyPassword
                      );
                    })
                    .catch((error) => {
                      this.$swal.fire({
                        icon: "error",
                        title: "Oops...",
                        html: error.message,
                      });
                    });
                }
              }
            }
          } catch (error) {
            this.$swal.fire({
              icon: "error",
              title: "Oops...",
              html: error.message,
            });
          }
        } else {
          this.$swal.fire({
            icon: "error",
            title: "Oops...",
            html: "Passwords don't match!",
          });
        }
      }
    },
  },
};
</script>
